:local .card {
  background: #fff;
  border: solid 1px #e7ecee;
  padding: 20px;
  text-align: center;
  width: 256px;
  height: 243px;
  margin: 0 5px 10px 5px; }

:local .cardAvatar {
  width: 50px;
  height: 50px;
  margin-bottom: 3px;
  display: inline-block;
  position: relative;
  overflow: hidden;
  font-size: 10px; }
  :local .cardAvatar img {
    object-fit: cover;
    position: absolute;
    border-radius: 50%;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%; }

:local .cardName {
  font-size: 16px;
  line-height: 1.5;
  color: #333;
  margin-bottom: 5px;
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }
  :local .cardName:hover {
    text-decoration: underline !important;
    color: #333; }

:local .cardPosition {
  font-size: 13px;
  font-weight: 400;
  line-height: 1.54;
  color: #666;
  margin-bottom: 8px;
  height: 40px; }

:local .cardStat {
  font-size: 12px;
  line-height: 1.67;
  color: #999;
  margin-bottom: 16px; }

:local .cardButton {
  width: 100px;
  height: 26px;
  text-transform: uppercase;
  font-size: 13px;
  margin: auto;
  background: transparent;
  color: #009fc6;
  border: 1px solid #009fc6;
  font-weight: 600;
  cursor: pointer;
  border-radius: 3px; }

:local .viewMore {
  background-color: rgba(0, 0, 0, 0.05);
  color: #555;
  display: block;
  padding: 10px;
  text-align: center;
  width: 100%;
  cursor: pointer;
  text-transform: uppercase; }
